import React, { useState, useEffect } from "react";

import Post from "./Post";
import PostInfo from "./PostInfo";
import "./ProfileFeed.css"

const ProfileFeed = () => {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    //   , setPosts useEffect  
    setPosts([
      {
        postId: 1,
        userId: "fub2fub",
        nickname: "",
        identifier: true,
        date: "2020-01-04 12:23:56",
        content: "",
      },
      {
        postId: 12,
        userId: "fub2fub",
        nickname: "",
        identifier: true,
        bio: "",
        date: "2020-02-04 10:53:26",
        content: "",
      },
    ]);
  }, []);

  return (
    <div className="profilefeed">
      <div className="profilefeed__header">
        <p className="profilefeed__nickname"></p>
        <p className="tweetsCount">2 </p>
      </div>

      <PostInfo />

      <div className="profilefeed__cate">
        <p className="cate__text active"></p>
        <p className="cate__text">  </p>
        <p className="cate__text"></p>
        <p className="cate__text"> </p>
      </div>

      {posts.map((post) => (
        <Post
          key={post.postId}
          nickname={post.nickname}
          userId={post.userId}
          identifier={post.identifier}
          content={post.content}
          date={post.date}
        />
      ))}
    </div>
  );
};

export default ProfileFeed;
