import React from "react";

import SidebarCate from "./SidebarCate";

import "./Sidebar.css" 

import TwitterIcon from "@material-ui/icons/Twitter";
import HomeIcon from "@material-ui/icons/Home";
import SearchIcon from "@material-ui/icons/Search";
import NotificationsNoneIcon from "@material-ui/icons/NotificationsNone";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import ListAltIcon from "@material-ui/icons/ListAlt";
import PermIdentityIcon from "@material-ui/icons/PermIdentity";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";

const Sidebar = () => {
  return (
    <div className="sidebar">
      <div className="sidebar__top">
        <div className="twitterIcon__box">
          <TwitterIcon className="twitterIcon" />
        </div>

        <div>
          <SidebarCate Icon={HomeIcon} text="" isLink={1}></SidebarCate>
          <SidebarCate Icon={SearchIcon} text="" isLink={0}/>
          <SidebarCate Icon={NotificationsNoneIcon} text="" isLink={0}/>
          <SidebarCate Icon={MailOutlineIcon} text="" isLink={0}/>
          <SidebarCate Icon={BookmarkBorderIcon} text="" isLink={0}/>
          <SidebarCate Icon={ListAltIcon} text="" isLink={0}/>
          <SidebarCate Icon={PermIdentityIcon} text="" isLink={2}></SidebarCate>
          <SidebarCate Icon={MoreHorizIcon} text=" " isLink={0}/>
        </div>

        <button className="sidebar__tweetbutton"></button>
      </div>

      <div className="sidebar__bottom">
        <div className="bottom__left">
          <img className="bottom__profileimg" src="" />
        </div>
        <div className="bottom__right">
          <p className="bottom__nickname"></p>
          <p className="bottom__userid">@fub2fub</p>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
